/* Animation for smooth transitions */
.dashboardPreview {
  animation: slideUp 0.8s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Custom scrollbar */
.dashboardMain::-webkit-scrollbar {
  width: 6px;
}

.dashboardMain::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.dashboardMain::-webkit-scrollbar-thumb {
  background: #1890ff;
  border-radius: 3px;
}

.dashboardMain::-webkit-scrollbar-thumb:hover {
  background: #722ed1;
}

/* Electron-specific scrolling fixes */
@media screen and (-webkit-appearance: none) {
  .landingPage {
    height: 100vh;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
  
  html, body {
    height: 100vh;
    overflow: hidden;
  }
  
  .landingPage {
    overflow-y: auto;
    height: 100vh;
  }
}

/* Electron-specific scrolling fixes are now handled by LandingPageLayout */

/* Additional Electron scrolling fixes */
.landingPage {
  scroll-behavior: smooth;
}

/* Ensure all sections are properly contained */
.heroSection,
.dashboardPreview,
.additionalContent,
.landingFooter {
  position: relative;
  z-index: 1;
}
